import Warp from 'warpjs';

class AnimateSVG {
  constructor(el) {
    this.warp = new Warp(el);

    this.warp.interpolate(50);
    this.warp.transform(([x, y]) => [x, y, y]);

    this.offset = 0;
    this.animate();
  }

  animate() {
    // eslint-disable-next-line no-unused-vars
    this.warp.transform(([x, y, oy]) =>
      [x, oy + (5 * Math.sin((x / 16) + this.offset)), oy]);
    this.offset += 0.01;
    requestAnimationFrame(this.animate.bind(this));
  }
}

export default AnimateSVG;
