import '../../scss/page/loader.scss';
import AnimateSVG from '../class/AnimateSVG';

if (module.hot) {
  module.hot.accept();
}

(function definePortfolio() {
  const portfolio = {
    portfolioList: false,
    filterButtons: false,
    gridWrapper: false,
    stageOptions: ['seed', 'venturegrowth', 'exits', 'allstages'],
    // eslint-disable-next-line max-len
    sectorOptions: ['biohealth', 'crypto', 'consumer', 'enterprise', 'allsectors'],

    initialize() {
      const self = this;
      self.portfolioList = document.getElementById(
        'portfolio-landing-company-list'
      );
      self.filterButtons = document.getElementsByClassName(
        'portfolio-filter__option-button'
      );
      self.gridWrapper = document.getElementById('portfolio__list--grid');

      if (self.portfolioList && self.filterButtons) {
        Array.from(self.filterButtons).forEach((el) =>
          el.addEventListener('click', self.updateFilters)
        );
        window.addEventListener('hashchange', self.refreshFilters);
        self.refreshFilters();
      }
    },

    updateFilters() {
      const { filter } = this.dataset;
      const hash = window.location.hash.substring(1);

      let hashArray = [];
      if (hash) {
        hashArray = hash.split(',');
      }

      if (- 1 !== portfolio.stageOptions.indexOf(filter)) {
        hashArray = hashArray.filter(
          (x) => - 1 === portfolio.stageOptions.indexOf(x)
        );
      } else if (- 1 !== portfolio.sectorOptions.indexOf(filter)) {
        hashArray = hashArray.filter(
          (x) => - 1 === portfolio.sectorOptions.indexOf(x)
        );
      }

      if ('allstages' !== filter && 'allsectors' !== filter) {
        hashArray.push(filter);
      }
      window.location.hash = `#${hashArray.join()}`;
    },

    refreshFilters() {
      const hash = window.location.hash.substring(1);

      let hashArray = [];
      if (hash) {
        hashArray = hash.split(',');
      }

      Array.from(portfolio.filterButtons).forEach((el) =>
        el.classList.remove('filter-active')
      );

      let stageButton = document.getElementById('filter--allstages');
      stageButton.classList.add('filter-active');
      portfolio.stageOptions.forEach((option) => {
        if (- 1 !== hashArray.indexOf(option)) {
          stageButton.classList.remove('filter-active');
          stageButton = document.getElementById(`filter--${option}`);
          stageButton.classList.add('filter-active');
        }
      });

      let sectorButton = document.getElementById('filter--allsectors');
      sectorButton.classList.add('filter-active');
      portfolio.sectorOptions.forEach((option) => {
        if (- 1 !== hashArray.indexOf(option)) {
          sectorButton.classList.remove('filter-active');
          sectorButton = document.getElementById(`filter--${option}`);
          sectorButton.classList.add('filter-active');
        }
      });

      portfolio.stageOptions.forEach((option) =>
        portfolio.portfolioList.classList.remove(`company-filter--${option}`)
      );
      portfolio.sectorOptions.forEach((option) =>
        portfolio.portfolioList.classList.remove(`company-filter--${option}`)
      );

      if (hashArray.length) {
        hashArray.forEach((option) =>
          portfolio.portfolioList.classList.add(`company-filter--${option}`)
        );
      }
    },
  };

  document.addEventListener('DOMContentLoaded', () => {
    portfolio.initialize();
  });
}());

/**
 * On the portfolio page, add event listeners to show and hide the filters on
 * mobile devices.
 */
(function collapseFilters() {
  const filters = {
    /**
     * Media query list to hide and remove aria attributes fron desktop.
     * @see  client/scss/core/_breakpoints.scss
     * @type MediaQueryList
     */
    mql: window.matchMedia('(max-width: 1024px)'),

    /**
     * Selectors used within this class.
     * @type {Object}
     */
    classNames: {
      wrapper: 'portfolio-filter',
      list: 'portfolio-filter__options',
      button: 'portfolio-filters__toggle',
    },

    /**
     * Run after document content loaded.
     */
    initialize() {
      // Apply interactivity to buttons.
      this.addListeners();

      // Update the list visibility based on window width.
      this.updateListVisibility(this.mql.matches);

      // Add listener to when the window resizes to update the list visibility.
      this.mql.addListener((mql) => this.updateListVisibility(mql.matches));
    },

    /**
     * Apply interactivity to buttons.
     */
    addListeners() {
      const self = this;
      const {
        wrapper: wrapperSelector,
        list: listSelector,
        button: buttonSelector,
      } = this.classNames;

      // Find lists that will open and close.
      const filterLists = document.querySelectorAll(`.${wrapperSelector}`);

      // Add attributes to hide the menu and return button that controls the list.
      const buttons = Array.prototype.map
        .call(filterLists, (wrapper) => {
          // Add hidden attribute to the lists.
          const list = wrapper.querySelector(`.${listSelector}`);
          if (! list) {
            return false;
          }

          // Hidden on load by default.
          list.setAttribute('aria-hidden', 'true');

          // Find the button that controls this list and return it.
          const button = wrapper.querySelector(`.${buttonSelector}`);
          return button;
        })
        // Remove any non-element buttons
        .filter((button) => button);

      // Apply listeners to buttons.
      buttons.forEach((button) => {
        button.addEventListener('click', self.toggleList);
      });
    },

    /**
     * Update whether or not the list is expanded.
     *
     * @param  {Object} options.target  HTMLElement that is being clicked
     */
    toggleList({ target }) {
      const id = target.getAttribute('aria-controls');
      const list = document.getElementById(id);
      const isHidden = 'true' === list.getAttribute('aria-hidden');
      const isPresssed = 'true' === target.getAttribute('aria-pressed');
      list.setAttribute('aria-hidden', ! isHidden);
      target.setAttribute('aria-pressed', ! isPresssed);
    },

    /**
     * Update all the list visibility based on paramenter.
     *
     * @param  {Boolean} shouldHide What list's aria-hidden attribute shoudl be set to.
     */
    updateListVisibility(shouldHide) {
      const { list: listSelector } = this.classNames;

      // Find all collapseed lists.
      const lists = document.querySelectorAll(`.${listSelector}`);

      const ariaHidden = shouldHide ? 'true' : 'false';

      // Set state on all lists.
      Array.prototype.forEach.call(
        lists,
        // If media query matches (desktop), they are expanded. Otherwise, keep them hidden.
        (list) => list.setAttribute('aria-hidden', ariaHidden)
      );
    },
  };

  document.addEventListener('DOMContentLoaded', () => {
    filters.initialize();
    const sectionHeaders = [...document.querySelectorAll(
      '.content-with-sidebar h1, ' +
      '.content-with-sidebar h2, ' +
      '.content-with-sidebar h3, ' +
      '.content-with-sidebar h4, .content-with-sidebar h5')];
    if (sectionHeaders.length) {
      const currentHref = window.location.href;
      sectionHeaders.forEach((headerItem) => {
        if (headerItem.id) {
          const url = `${currentHref}#${headerItem.id}`;
          headerItem.setAttribute('data-clipboard-text', url);
          headerItem.addEventListener('click', (event) => {
            const input = document.createElement('textarea');
            input.innerHTML = event.currentTarget.dataset.clipboardText;
            document.body.appendChild(input);
            input.select();
            document.execCommand('copy');
            document.body.removeChild(input);
            // eslint-disable-next-line no-alert
            alert('Section Link Copied to Clipboard');
          });
        }
      });
    }
    const animateElSVG = document.getElementById('svg-element-animate-liquid');
    if (animateElSVG) {
      // eslint-disable-next-line no-new
      new AnimateSVG(animateElSVG);
    }
  });
}());
